.header{
    background-color: $primary-light;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 29.5px 88px;

    @include pad(){
      padding: 30px 39px;
    }

    @include mobile(){
      padding: 0 0px;
      flex-direction: column;
      justify-content: center;
    }
  }

      .logo{
        @include mobile(){
          margin: 10px 0;
        }

        a{
          background: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/logo.png');
          background-repeat: no-repeat;
          width: 80px;
          height: 49px;
          @include hideText;

          @include mobile(){
            background-size: contain;
            width: 48px;
            height: 30px;
          }
        } 
      }

      .menu{
            display: flex;

            @include mobile(){
              width:100%;
              flex-wrap: wrap;
              justify-content: center;
            }

            li{
              margin-left: 40px;

              @include mobile(){
                width: 50%;
                text-align: center;
                padding: 8px 54px;
                margin-left: 0;
                border: 1px solid white;
              }
            }
          
          a{
            color:$layout-font-color;
            font-size: $fz-3;
            @include linkSetting;
          }

      }